"use client";

import React, { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { Search, Filter, Loader2 } from "lucide-react";
import { Input } from "@/components/ui/Input";
import { Button } from "@/components/ui/Button";
import { Card } from "@/components/ui/Card";
import { formatCurrency } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";
import type { Product } from "@/lib/product-service";

export default function ProductsPage() {
    const [searchQuery, setSearchQuery] = useState("");
    const { addItem } = useCart();
    const [selectedCategory, setSelectedCategory] = useState("All");
    const [products, setProducts] = useState<Product[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetch("/api/products")
            .then(res => res.json())
            .then(data => setProducts(data))
            .catch(err => console.error(err))
            .finally(() => setLoading(false));
    }, []);

    const categories = ["All", ...Array.from(new Set(products.map(p => p.category)))];

    const filteredProducts = products.filter(product => {
        const matchesSearch = product.name.toLowerCase().includes(searchQuery.toLowerCase());
        const matchesCategory = selectedCategory === "All" || product.category === selectedCategory;
        return matchesSearch && matchesCategory;
    });

    if (loading) return (
        <div className="flex items-center justify-center min-h-[50vh]">
            <Loader2 className="w-8 h-8 animate-spin text-primary" />
        </div>
    );

    return (
        <div className="container-custom py-12">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-12 gap-6">
                <div>
                    <h1 className="text-4xl font-display font-bold mb-2">Our Collection</h1>
                    <p className="text-text-secondary">Curated premium products for you.</p>
                </div>

                <div className="flex flex-col sm:flex-row gap-4 w-full md:w-auto">
                    <Input
                        placeholder="Search products..."
                        leftIcon={<Search className="w-4 h-4" />}
                        className="w-full sm:w-64"
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                    />
                    <div className="flex gap-2 overflow-x-auto pb-2 sm:pb-0">
                        {categories.map(cat => (
                            <button
                                key={cat}
                                onClick={() => setSelectedCategory(cat)}
                                className={`px-4 py-2 rounded-lg text-sm font-medium whitespace-nowrap transition-colors ${selectedCategory === cat
                                    ? "bg-primary text-white"
                                    : "bg-surface-elevated text-text-secondary hover:bg-surface-elevated/80"
                                    }`}
                            >
                                {cat}
                            </button>
                        ))}
                    </div>
                </div>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
                {filteredProducts.map((product) => (
                    <Card
                        key={product.id}
                        hoverEffect
                        className="group overflow-hidden flex flex-col h-full"
                    >
                        <div className="relative aspect-square overflow-hidden rounded-t-lg -mx-6 -mt-6 mb-6 bg-surface-elevated">
                            {product.image_url ? (
                                <Image
                                    src={product.image_url}
                                    alt={product.name}
                                    fill
                                    className="object-cover transition-transform duration-500 group-hover:scale-110"
                                />
                            ) : (
                                <div className="w-full h-full flex items-center justify-center bg-surface-elevated text-text-tertiary">
                                    No Image
                                </div>
                            )}
                            <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center gap-4">
                                <Button
                                    size="sm"
                                    onClick={() => addItem(product)}
                                    className="translate-y-4 group-hover:translate-y-0 transition-transform duration-300"
                                >
                                    Quick Add
                                </Button>
                                <Link href={`/products/${product.id}`}>
                                    <Button
                                        variant="secondary"
                                        size="sm"
                                        className="translate-y-4 group-hover:translate-y-0 transition-transform duration-300 delay-75"
                                    >
                                        View Details
                                    </Button>
                                </Link>
                            </div>
                        </div>

                        <div className="flex-grow">
                            <div className="text-xs font-bold text-primary mb-2 uppercase tracking-wide">
                                {product.category}
                            </div>
                            <h3 className="text-xl font-bold mb-2 line-clamp-1">{product.name}</h3>
                            <p className="text-text-secondary text-sm mb-4 line-clamp-2">
                                {product.description}
                            </p>
                        </div>

                        <div className="flex items-center justify-between mt-auto pt-4 border-t border-border/50">
                            <span className="text-lg font-bold">
                                {formatCurrency(product.price)}
                            </span>
                            <button
                                onClick={() => addItem(product)}
                                className="text-sm font-medium text-primary hover:text-primary-dark transition-colors"
                            >
                                Add to Cart
                            </button>
                        </div>
                    </Card>
                ))}
            </div>

            {filteredProducts.length === 0 && (
                <div className="text-center py-20">
                    <div className="inline-flex justify-center items-center w-16 h-16 rounded-full bg-surface-elevated mb-4">
                        <Search className="w-8 h-8 text-text-tertiary" />
                    </div>
                    <h3 className="text-xl font-bold mb-2">No products found</h3>
                    <p className="text-text-secondary">
                        Try adjusting your search or category filter.
                    </p>
                </div>
            )}
        </div>
    );
}
